@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;400;600&display=swap);
body {
	margin: 0;
	font-family: 'Raleway', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
		'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root{--main-color: #9868f1}
.grid{margin:25px 30px}.grid_rows{display:flex}
.node{display:block;border:1px solid #272229;height:28px;width:28px}.node:hover{background-color:#c7c7c7}.node.end{background-color:red}.node.start{background-color:green}.node.wall{background-color:#000}.node-visited{-webkit-animation-name:visitedNodeAnimation;animation-name:visitedNodeAnimation;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-delay:0;animation-delay:0;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:running;animation-play-state:running}@-webkit-keyframes visitedNodeAnimation{0%{-webkit-transform:scale(0.3);transform:scale(0.3);background-color:#241342;border-radius:100%}50%{background-color:#c36fce}75%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#6e75d1;border:1px solid #6e75d1}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#9868f1;border:1px solid #4d3d6b}}@keyframes visitedNodeAnimation{0%{-webkit-transform:scale(0.3);transform:scale(0.3);background-color:#241342;border-radius:100%}50%{background-color:#c36fce}75%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#6e75d1;border:1px solid #6e75d1}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#9868f1;border:1px solid #4d3d6b}}.node-shortest-path{-webkit-animation-name:shortestPath;animation-name:shortestPath;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-delay:0;animation-delay:0;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:running;animation-play-state:running}@-webkit-keyframes shortestPath{0%{-webkit-transform:scale(0.6);transform:scale(0.6);background-color:#e9a7f1;border:1px solid #ce84d6}50%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#e9a7f1;border:1px solid #b059b9}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#f8d6fb;border:1px solid #8c2497}}@keyframes shortestPath{0%{-webkit-transform:scale(0.6);transform:scale(0.6);background-color:#e9a7f1;border:1px solid #ce84d6}50%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#e9a7f1;border:1px solid #b059b9}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#f8d6fb;border:1px solid #8c2497}}.right-click{position:absolute;background:#fff;border:1px solid #d4d4d4;border-radius:4px}.right-click-option{display:flex;align-items:center;justify-content:space-between;padding:5px 10px;border-radius:4px;cursor:default}.right-click-option:hover{background:skyblue;color:#fff}
.navbar{background-color:var(--main-color)}.navbar .container{padding:0px 10px;display:flex;align-items:center;justify-content:space-between;height:65px}.navbar .container .left{display:flex;align-items:center;margin-left:20px}.navbar .container .left .logo{display:flex;align-items:center}.navbar .container .left .logo img{width:40px;margin-right:10px}.navbar .container .left .logo span{color:#f3effd;font-size:xx-large;font-weight:bold}.navbar .container .left .mazes{margin:0px 20px;font-size:large}.navbar .container .left .mazes .options{display:none;background-color:var(--main-color)}.navbar .container .left .mazes span{padding:10px;cursor:pointer}.navbar .container .left .mazes:hover{z-index:999}.navbar .container .left .mazes:hover .options{display:flex;flex-direction:column;position:absolute;margin-top:10px}.navbar .container .left .mazes:hover span:hover{color:#fff}.navbar .container .left .vizualize{padding:6px 16px;font-size:large;margin-left:6px;background-color:var(--main-color);border:2px solid var(--main-color);border-radius:4px;color:#fff;cursor:pointer}.navbar .container .left .vizualize:hover{background-color:#fff;color:var(--main-color)}.navbar .container .right{display:flex;align-items:center;justify-content:space-between;margin-right:20px}.navbar .container .right .icon{margin:0px 10px;cursor:pointer;font-size:30px}.navbar .container .right .tooltip-class{font-size:20px !important;pointer-events:auto !important;opacity:.8 !important}.navbar .container .right .tooltip-class:hover{visibility:visible !important;opacity:1 !important}
.container{display:flex;align-items:center;justify-content:space-between}.container .pathfindings{margin:0px 20px;font-size:large}.container .pathfindings .options{display:none;background-color:var(--main-color)}.container .pathfindings span{padding:10px;cursor:pointer}.container .pathfindings:hover{z-index:999}.container .pathfindings:hover .options{display:flex;flex-direction:column;position:absolute;margin-top:10px}.container .pathfindings:hover span:hover{color:#fff}.container .vizualize{padding:6px 16px;font-size:large;margin-left:6px;background-color:var(--main-color);border:2px solid var(--main-color);border-radius:4px;color:#fff;cursor:pointer}.container .vizualize:hover{background-color:#fff;color:var(--main-color)}
.home{overflow:hidden}
